.card {
    width: 45%;
    height: 10%;
    padding: 60px;
    margin: 0 auto;
    margin-top: 100px;
    background-color: white;
    border-radius: 5px;
    color: black;
    display: flex;
    align-items: center;
    justify-content: center;
}

.card {
    transition: 0.3s;
}

.card:hover {
    box-shadow: 0 13px 27px -5px rgba(50, 50, 93, 0.25),
    0 8px 16px -8px rgba(0, 0, 0, 0.3), 0 -6px 16px -6px rgba(0, 0, 0, 0.025);
    transform: translateY(-5%);
}